import React from 'react'

import {
  AllergenEnum,
  FieldStateEnum,
  WareUnitEnum,
} from '../../../graph/generated'
import { GMenuStockDefaults, GMenuStockForm } from '../../../graph/types'
import { RM } from '../../../helpers/ramda'
import { MenuBaseFormLoader } from '../atoms/MenuBaseFormLoader'
import { useMenu } from '../hooks/useMenu'
import { MenuStockDefaultsQuery, MenuStockFormQuery } from './StockForm.graphql'

interface IProps {
  isCreate: boolean
  stockId: ID
  children: ReactNode
}

export type TStockFormValues = {
  id: ID
  code: string
  plu: Maybe<string>
  note: string
  nameLabel: string
  internalNote: string
  wareUnit: Maybe<WareUnitEnum>
  allergens: AllergenEnum[]
  allowNegative: FieldStateEnum
  allowNegativeDefault: FieldStateEnum
}

export const StockFormLoader: React.FC<IProps> = ({
  isCreate,
  stockId,
  children,
}) => {
  const menu = useMenu()
  return (
    <MenuBaseFormLoader<TStockFormValues>
      isCreate={isCreate}
      onCreate={values => menu.stock.createItem(values)}
      onUpdate={values => menu.stock.updateItem(values)}
      getDefaultValues={getDefaults}
      loadDefaults={async () => {
        const { data } = await menu.client.query<
          GMenuStockDefaults.Query,
          GMenuStockDefaults.Variables
        >({
          query: MenuStockDefaultsQuery,
          variables: { branchId: menu.stock.branchId },
          fetchPolicy: 'no-cache',
        })
        return {
          allowNegativeDefault: getAllowNegativeDefault(
            data.companyBranch.allowNegative,
          ),
        }
      }}
      loadValues={async () => {
        const { data } = await menu.client.query<
          GMenuStockForm.Query,
          GMenuStockForm.Variables
        >({
          query: MenuStockFormQuery,
          variables: { stockId },
          fetchPolicy: 'no-cache',
        })
        return getFormValues(data.stockItem)
      }}
    >
      {children}
    </MenuBaseFormLoader>
  )
}

function getDefaults(): TStockFormValues {
  return {
    id: 'NEW',
    internalNote: '',
    nameLabel: '',
    code: '',
    plu: '',
    note: '',
    wareUnit: null,
    allergens: [],
    allowNegative: FieldStateEnum.Default,
    allowNegativeDefault: FieldStateEnum.Default,
  }
}

function getFormValues(stockItem: GMenuStockForm.StockItem): TStockFormValues {
  return {
    id: stockItem.id,
    internalNote: 'TBD',
    nameLabel: stockItem.nameLabel,
    code: stockItem.code,
    note: stockItem.note,
    plu: stockItem.plu || '',
    wareUnit: stockItem.wareUnit.enum,
    allergens: stockItem.allergens.map(RM.prop('enum')),
    allowNegative: stockItem.allowNegative,
    allowNegativeDefault: getAllowNegativeDefault(
      stockItem.companyBranch.allowNegative,
    ),
  }
}

function getAllowNegativeDefault(isEnabled: boolean) {
  return isEnabled ? FieldStateEnum.Default : FieldStateEnum.Disabled
}
