import React, { useEffect, useState } from 'react'
import { Form, FormProps } from 'react-form'

interface IProps<TValues = object> extends FormProps<TValues> {
  shouldLoad: boolean
  loader(): Promise<TValues>
}

export function FormLoader<TValues = object>({
  shouldLoad,
  loader,
  ...props
}: IProps<TValues>) {
  const [values, setValues] = useState<TValues | null>(null)
  useEffect(() => {
    if (!shouldLoad) {
      return
    }
    loader().then(setValues)
  }, [loader, shouldLoad])
  return <Form values={values} {...props} />
}
