import React from 'react'
import { Field, FieldType } from 'react-form'

interface IProps {
  field: FieldType
  className?: string
  children?: never
}

export const FormDisplayField: React.FC<IProps> = ({ field, className }) => {
  return (
    <Field field={field}>
      {fieldApi => <span className={className}>{fieldApi.value}</span>}
    </Field>
  )
}
