import React from 'react'
import { Field, IFieldApi } from 'react-form'

import { VerticalGap } from '../../../atoms/VerticalGap'
import { GMenuAssocRecipe } from '../../../graph/types'
import { RM } from '../../../helpers/ramda'
import { WithSettings } from '../../settings/atoms/WithSettings'
import { MenuSuggestionPanel } from '../atoms/MenuSuggestionPanel'
import { TAssocRecipe, TPriceType } from '../menutypes'
import { RecipeAssocListTable } from '../molecules/RecipeAssocListTable'
import { QueryMenuRecipeFormAssoc } from './RecipeForm.graphql'

interface IProps {
  recipeId: MaybeID
  priceType: TPriceType
}

export const RecipeAssociatedView: React.FC<IProps> = ({
  priceType,
  recipeId,
}) => (
  <Field field="associatedRecipes">
    {(fieldApi: IFieldApi<TAssocRecipe[]>) => {
      const priceTypeRecipes = RM.filter(
        RM.propEq('warePriceType', priceType),
        fieldApi.value,
      )
      const isAssociated = (id: ID) =>
        priceTypeRecipes.some(RM.propEq('associatedRecipe', id))

      const rejectSuggestions = RM.reject(
        (recipe: GMenuAssocRecipe.Fragment) =>
          recipe.id === recipeId || isAssociated(recipe.id),
      )
      const onAddRecipe = (addedRecipe: GMenuAssocRecipe.Fragment) => {
        fieldApi.setValue(
          fieldApi.value.concat({
            warePriceType: priceType,
            associatedRecipe: addedRecipe.id,
            nameLabel: addedRecipe.nameLabel!,
            formattedPrice: '?',
          }),
        )
      }
      const onRemoveRecipe = (id: ID) => {
        fieldApi.setValue(
          RM.reject(
            ({ associatedRecipe, warePriceType }) =>
              associatedRecipe === id && warePriceType === priceType,
            fieldApi.value,
          ),
        )
      }

      return (
        <>
          <RecipeAssocListTable
            recipes={priceTypeRecipes}
            onRemove={onRemoveRecipe}
          />
          <WithSettings
            render={settings => (
              <QueryMenuRecipeFormAssoc
                variables={{
                  branchId: settings.branchId,
                }}
                fetchPolicy="cache-and-network"
                render={({ recipes }) => (
                  <MenuSuggestionPanel
                    options={rejectSuggestions(recipes)}
                    onAdd={onAddRecipe}
                    // labelKey="nameLabel"
                  />
                )}
              />
            )}
          />
          <VerticalGap />
        </>
      )
    }}
  </Field>
)
