import React from 'react'

import {
  OrderOriginCategoryEnum,
  QMenuCategoryDocument,
  QMenuCategoryQuery,
  WareCategoryTypeEnum,
} from '../../../graph/generated'
import { MenuBaseFormLoader } from '../atoms/MenuBaseFormLoader'
import { useMenu } from '../hooks/useMenu'
import { TCategoryFormValues } from '../models/MenuCategoryModel'

interface IProps {
  isCreate: boolean
  categoryId: Maybe<ID>
  categoryType: WareCategoryTypeEnum
  children: ReactNode
}

export const CategoryFormLoader: React.FC<IProps> = ({
  isCreate,
  categoryId,
  categoryType,
  children,
}) => {
  const menu = useMenu()
  return (
    <MenuBaseFormLoader<TCategoryFormValues>
      isCreate={isCreate}
      onCreate={values => menu.category.createCategory(values, categoryType)}
      onUpdate={values => menu.category.updateCategory(values, categoryType)}
      getDefaultValues={() => getDefaults(categoryType)}
      loadValues={async () => {
        const { data } = await menu.client.query<QMenuCategoryQuery>({
          query: QMenuCategoryDocument,
          variables: { categoryId },
          fetchPolicy: 'no-cache',
        })
        return {
          ...data.category,
          origins: data.category.origins as OrderOriginCategoryEnum[],
        }
      }}
    >
      {children}
    </MenuBaseFormLoader>
  )
}

function getDefaults(categoryType: WareCategoryTypeEnum): TCategoryFormValues {
  return {
    id: 'NEW',
    visible: true,
    internalNoteLabel: '',
    pictogram: '',
    nameLabel: '',
    minCount: 0,
    maxCount: 0,
    categoryType: { enum: categoryType },
    recipes: [],
    origins: [],
  }
}
