import { Trans } from '@lingui/macro'
import React from 'react'
import { FormApi } from 'react-form'
import { Redirect, RouteComponentProps } from 'react-router'
import styled from 'styled-components/macro'

import { CanMutate } from '../../../atoms/CanMutate'
import { Card } from '../../../atoms/Card'
import { Headline } from '../../../atoms/Headline'
import { FullPageLayout } from '../../../molecules/FullPageLayout'
import { PageLayoutFullHeightItem } from '../../../molecules/PageLayoutFullHeightItem'
import { TopRightButtonContainer } from '../../../molecules/TopRightButtonContainer'
import { IsCategoryType } from '../atoms/IsCategoryType'
import { useMenuRouting } from '../hooks/useMenuRouting'
import { getCategoryTypeLabel } from '../labels'
import { SubmitButton } from '../molecules/SubmitButton'
import { CategoryBasicForm } from '../organisms/CategoryFormBasic'
import { CategoryFormLoader } from '../organisms/CategoryFormLoader'
import { CategoryRecipesView } from '../organisms/CategoryRecipesView'
import { CategoryRequirementsForm } from '../organisms/CategoryRequirementsForm'

interface IProps extends RouteComponentProps<{ categoryId: ID }> {}

export const FormContainer = styled.div`
  display: flex;
  flex-direction: column;
  height: 100%;
  width: 100%;
`
export const FormFixedRow = styled.div`
  flex: 0 0 8rem;
`
export const FormStretchedRow = styled.div`
  flex: 1 1 0;
  min-height: 0;
  position: relative;
`

export const MenuCategoryPage: React.FC<IProps> = ({ match }) => {
  const { categoryId } = match.params
  const {
    isCreate,
    itemType: categoryType,
    getCategoryListRoute,
  } = useMenuRouting(categoryId)

  if (categoryType === null) {
    return <Redirect to={getCategoryListRoute()} />
  }

  return (
    <CategoryFormLoader
      isCreate={isCreate}
      categoryId={categoryId}
      categoryType={categoryType}
    >
      <FullPageLayout
        pageTitle={getCategoryTypeLabel(categoryType)}
        hideLogoutWidget={true}
        useBackButton={true}
      >
        <PageLayoutFullHeightItem forcePercentHeightInMobile={100}>
          <FormContainer>
            <FormFixedRow>
              <Card>
                <CategoryBasicForm categoryType={categoryType} />
              </Card>
            </FormFixedRow>
            <IsCategoryType isSideDish={true} type={categoryType}>
              <FormFixedRow>
                <Card>
                  <Headline>
                    <Trans>Required amounts of recipes to be picked</Trans>
                  </Headline>
                  <FormApi component={CategoryRequirementsForm} />
                </Card>
              </FormFixedRow>
            </IsCategoryType>
            <FormStretchedRow>
              <Card stretchToParent={true}>
                <Headline>
                  <Trans>Recipes assigned to this category</Trans>
                </Headline>
                <CategoryRecipesView categoryType={categoryType} />
              </Card>
            </FormStretchedRow>
          </FormContainer>
        </PageLayoutFullHeightItem>

        <TopRightButtonContainer>
          <CanMutate
            mutationName={
              categoryId === null ? 'createWareCategory' : 'updateWareCategory'
            }
          >
            <SubmitButton isCreate={categoryId === null} />
          </CanMutate>
        </TopRightButtonContainer>
      </FullPageLayout>
    </CategoryFormLoader>
  )
}
