import React from 'react'
import { Field } from 'react-form'
import { Route } from 'react-router-dom'

import { TouchButtonDelete } from '../../../molecules/TouchButtonDelete'

interface IProps {
  confirmText: string
  onDelete(id: ID): Promise<void>
}

export const FormDeleteButton: React.FC<IProps> = ({
  confirmText,
  onDelete,
}) => (
  <Field field="id">
    {field =>
      field.value !== 'NEW' && (
        <Route>
          {({ history }) => (
            <TouchButtonDelete
              confirmText={confirmText}
              onDelete={async () => {
                await onDelete(field.value)
                history.goBack()
              }}
            />
          )}
        </Route>
      )
    }
  </Field>
)
