import React from 'react'
import { RouteComponentProps } from 'react-router'

import { CanMutate } from '../../../atoms/CanMutate'
import { CanQuery } from '../../../atoms/CanQuery'
import { Card } from '../../../atoms/Card'
import { i18n } from '../../../i18n'
import { FullPageLayout } from '../../../molecules/FullPageLayout'
import { TopRightButtonContainer } from '../../../molecules/TopRightButtonContainer'
import { useMenuRouting } from '../hooks/useMenuRouting'
import { SubmitButton } from '../molecules/SubmitButton'
import { StockFormLoader } from '../organisms/StockFormLoader'
import { StockFormMain } from '../organisms/StockFormMain'
import { StockRecipesView } from '../organisms/StockRecipesVIew'

interface IProps extends RouteComponentProps<{ stockId: ID }> {}

export const MenuStockPage: React.FC<IProps> = ({ match }) => {
  const { stockId } = match.params
  const { isCreate } = useMenuRouting(stockId)

  return (
    <FullPageLayout
      pageTitle={i18n.t`Stock card`}
      hideLogoutWidget={true}
      useBackButton={true}
    >
      <StockFormLoader isCreate={isCreate} stockId={stockId}>
        <>
          <TopRightButtonContainer>
            <CanMutate
              mutationName={isCreate ? 'createStockItem' : 'updateStockItem'}
            >
              <SubmitButton isCreate={isCreate} />
            </CanMutate>
          </TopRightButtonContainer>
          <Card>
            <StockFormMain />
            {!isCreate && (
              <CanQuery queryName="recipes">
                <StockRecipesView stockId={stockId} />
              </CanQuery>
            )}
          </Card>
        </>
      </StockFormLoader>
    </FullPageLayout>
  )
}
