import { observer } from 'mobx-react-lite'
import React from 'react'

import { useRoot } from '../../../atoms/RootProvider'
import {
  FMenuCategoryListItemFragment,
  useQMenuCategoryList,
  WareCategoryTypeEnum,
} from '../../../graph/generated'
import { RM } from '../../../helpers/ramda'
import { MenuSuggestionPanel } from '../atoms/MenuSuggestionPanel'

interface IProps {
  categoryType: WareCategoryTypeEnum
  onAdd(recipe: TCategory): void
  addButtonText: string
  currentCategories: TCategory[]
}

type TCategory = FMenuCategoryListItemFragment

const CategorySuggestionPanel: React.FC<IProps> = ({
  categoryType,
  currentCategories,
  addButtonText,
  onAdd,
}) => {
  const { settings } = useRoot()
  const { data, loading } = useQMenuCategoryList({
    branchId: settings.branchId,
    categoryType,
  })

  const categories = (data && data.categories) || []

  return (
    <MenuSuggestionPanel<TCategory>
      isLoading={loading}
      options={RM.without(currentCategories, categories)}
      addButtonText={addButtonText}
      onAdd={onAdd}
    />
  )
}

export default observer(CategorySuggestionPanel)
