import { i18nMark } from '@lingui/core'
import React, { useMemo } from 'react'
import { FormApi } from 'react-form'

import { FormInputFlatSelect } from '../../../atoms/FormInputFlatSelect'
import { IconLine } from '../../../atoms/IconLine'
import { FieldStateEnum } from '../../../graph/generated'
import { i18n } from '../../../i18n'

interface IProps {}

const items = [
  {
    type: FieldStateEnum.Enabled,
    label: i18nMark('Permit'),
  },
  {
    type: FieldStateEnum.Disabled,
    label: i18nMark('Forbid'),
  },
]

export const FormInputAllowNegative: React.FC<IProps> = () => {
  const labelForDefault = useMemo(
    () => ` (${i18n.t`Is default setting`.toLocaleLowerCase()})`,
    [],
  )
  return (
    <FormApi>
      {({ values: { allowNegativeDefault } }) => (
        <IconLine label={i18n.t`Negative stock`} labelWidth={7}>
          <FormInputFlatSelect
            field="allowNegative"
            items={items}
            formatItem={item => {
              if (item.type === allowNegativeDefault) {
                return (
                  <span>
                    {i18n._(item.label)}
                    <i>{labelForDefault}</i>
                  </span>
                )
              }
              return i18n._(item.label)
            }}
            getItemId={item => item.type}
          />
        </IconLine>
      )}
    </FormApi>
  )
}
