import React from 'react'

import { CardDock } from '../../../atoms/CardDock'
import { FormColumn, FormContainer } from '../../../atoms/FormContainer'
import { FormInputCheckbox } from '../../../atoms/FormInputCheckbox'
import { Sizer } from '../../../atoms/Sizer'
import { WareCategoryTypeEnum } from '../../../graph/generated'
import { i18n } from '../../../i18n'
import { WithSettings } from '../../settings/atoms/WithSettings'
import { MenuCategoryPictogramSelect } from '../atoms/MenuCategoryPictogramSelect'
import { FormDeleteButton } from '../molecules/FormDeleteButton'
import { FormInputInternalNote } from '../molecules/FormInputInternalNote'
import { FormInputName } from '../molecules/FormInputName'
import { MenuCategoryOrigins } from '../molecules/MenuCategoryOrigins'

interface ICategoryBasicFormProps {
  categoryType: WareCategoryTypeEnum
}

export const CategoryBasicForm: React.FC<ICategoryBasicFormProps> = ({
  categoryType,
}) => (
  <FormContainer>
    <FormColumn>
      <Sizer width={30}>
        <FormInputName
          placeholder={i18n.t`Category name`}
          inputFullWidth={true}
        />
      </Sizer>
      <Sizer width={30}>
        <FormInputInternalNote placeholder={i18n.t`Category internal note`} />
      </Sizer>
      <Sizer width={30}>
        <MenuCategoryOrigins />
      </Sizer>
      <FormInputCheckbox
        field="visible"
        label={i18n.t`To show this category`}
      />
    </FormColumn>

    {categoryType === WareCategoryTypeEnum.Main ? (
      <FormColumn>
        <MenuCategoryPictogramSelect field="pictogram" />
      </FormColumn>
    ) : null}

    <CardDock position="BottomRight">
      <WithSettings
        render={({ menu }) => (
          <FormDeleteButton
            confirmText={i18n.t`Remove this category?`}
            onDelete={id => menu.category.deleteCategory(id, categoryType)}
          />
        )}
      />
    </CardDock>
  </FormContainer>
)
