import { Popover } from '@material-ui/core'
import { isApolloError } from 'apollo-client/errors/ApolloError'
import React from 'react'
import styled from 'styled-components'

import { FormLoader } from '../../../atoms/FormLoader'
import { NavigateBack } from '../../../atoms/NavigateBack'
import { TQueryErrors } from '../../../graph/errorHandler'
import { logError, logForm } from '../../../helpers/logger'
import { RM } from '../../../helpers/ramda'
import { i18n } from '../../../i18n'

interface IProps<TValues = object> {
  isCreate: boolean
  getDefaultValues(): TValues
  loadValues(): Promise<TValues>
  loadDefaults?(): Promise<Partial<TValues>>
  onCreate(values: TValues): Promise<TQueryErrors>
  onUpdate(values: TValues): Promise<TQueryErrors>
}

interface IState {
  submitError: Maybe<Error>
  validationErrors: Maybe<object>
}

const ErrorPop = styled.div`
  color: red;
  padding: 1rem;
  max-width: 30rem;
  overflow-wrap: break-word;
`

export class MenuBaseFormLoader<TValues extends {}> extends React.Component<
  IProps<TValues>,
  IState
> {
  state: IState = { submitError: null, validationErrors: null }
  renderErrors() {
    const { submitError, validationErrors } = this.state
    if (submitError) {
      return this.renderSubmitError(submitError)
    }
    if (validationErrors) {
      return this.renderValidationErrors(validationErrors)
    }
    return null
  }
  renderSubmitError(submitError: Error) {
    let messages = [
      i18n.t`Unexpected error while sending form. Out team was notified about the issue.`,
    ]
    if (isApolloError(submitError)) {
      const filter =
        process.env.NODE_ENV === 'production'
          ? RM.propEq('showToUser', true)
          : () => true
      const userErrors = submitError.graphQLErrors
        .filter(filter)
        .map(RM.prop('message'))
      if (userErrors.length) {
        messages = userErrors
      }
    }
    return this.renderPopover(messages)
  }
  renderValidationErrors(errors: object) {
    return null // this.renderPopover(Object.values(errors))
  }
  renderPopover(messages: string[]) {
    return (
      <Popover
        open={true}
        anchorEl={() => document.getElementById('submit-button')!}
        onClose={() =>
          this.setState({ submitError: null, validationErrors: null })
        }
        anchorOrigin={{
          vertical: 'bottom',
          horizontal: 'center',
        }}
      >
        {messages.map((message, idx) => (
          <ErrorPop key={idx}>{message}</ErrorPop>
        ))}
      </Popover>
    )
  }
  render() {
    const {
      isCreate,
      onCreate,
      onUpdate,
      getDefaultValues,
      loadValues,
      loadDefaults,
      children,
    } = this.props
    return (
      <NavigateBack
        render={goBack => (
          <FormLoader<TValues>
            shouldLoad={true}
            defaultValues={getDefaultValues()}
            loader={async () => {
              const defaults = getDefaultValues()
              if (loadDefaults) {
                const loadedDefaults = await loadDefaults()
                Object.assign(defaults, loadedDefaults)
              }
              if (isCreate) {
                return defaults
              }
              const values = await loadValues()
              return Object.assign({}, defaults, values)
            }}
            onChange={formApi => logForm('new values', formApi.values)}
            onSubmit={async values => {
              if (isCreate) {
                await onCreate(values)
              } else {
                await onUpdate(values)
              }
              goBack()
            }}
            onSubmitFailure={(validationErrors, submitError) => {
              this.setState({ validationErrors, submitError })
              if (Object.keys(validationErrors).length) {
                logError('validation errors', validationErrors)
              }
              if (submitError) {
                logError('submit error', submitError)
              }
            }}
          >
            {children}
            {this.renderErrors()}
          </FormLoader>
        )}
      />
    )
  }
}
