import { useRouterContext } from '@speedlo/hooks'
import React from 'react'

import { WareCategoryTypeEnum } from '../../../graph/generated'
import { TCategoryType, TRecipeType } from '../menutypes'

export const MENU_ROUTE = '/settings/menu'
const RECIPE_ROUTE = `${MENU_ROUTE}/recipe`
const CATEGORY_ROUTE = `${MENU_ROUTE}/category`
const STOCK_ROUTE = `${MENU_ROUTE}/stock`
const CREATE_ROUTE = 'create'

export function useMenuRouting(itemId?: ID) {
  const { location, history } = useRouterContext()

  const itemType = React.useMemo(
    () =>
      new URLSearchParams(location.search).get('type') as Maybe<
        WareCategoryTypeEnum
      >,
    [location.search],
  )

  const isCreate = itemId === CREATE_ROUTE

  const getItemRoute = (itemId: ID, itemType?: WareCategoryTypeEnum) => {
    return itemType ? `${itemId}?type=${itemType}` : itemId
  }

  const getRecipeListRoute = () => RECIPE_ROUTE
  const getRecipeCreateRoute = (recipeType: TRecipeType) =>
    getItemRoute(`${RECIPE_ROUTE}/${CREATE_ROUTE}`, recipeType)
  const getRecipeEditRoute = (recipeId: ID, recipeType?: TRecipeType) =>
    getItemRoute(`${RECIPE_ROUTE}/${recipeId}`, recipeType)

  const getCategoryListRoute = () => CATEGORY_ROUTE
  const getCategoryCreateRoute = (categoryType: TCategoryType) =>
    getItemRoute(`${CATEGORY_ROUTE}/${CREATE_ROUTE}`, categoryType)
  const getCategoryEditRoute = (categoryId: ID, categoryType?: TCategoryType) =>
    getItemRoute(`${CATEGORY_ROUTE}/${categoryId}`, categoryType)

  const getStockListRoute = () => STOCK_ROUTE
  const getStockDepositRoute = () => `${STOCK_ROUTE}/deposit`
  const getStockTransferRoute = () => `${STOCK_ROUTE}/transfer`
  const getStockCreateRoute = () =>
    getItemRoute(`${STOCK_ROUTE}/${CREATE_ROUTE}`)
  const getStockEditRoute = (categoryId: ID, categoryType?: TCategoryType) =>
    getItemRoute(`${STOCK_ROUTE}/${categoryId}`, categoryType)

  const redirectToRecipeList = () => {
    history.replace(getRecipeListRoute())
  }
  const redirectToCategoryList = () => {
    history.replace(getCategoryListRoute())
  }
  const redirectToStockList = () => {
    history.replace(getStockListRoute())
  }

  return {
    itemType,
    isCreate,
    getItemRoute,
    getRecipeListRoute,
    getRecipeCreateRoute,
    getRecipeEditRoute,
    getCategoryListRoute,
    getCategoryCreateRoute,
    getCategoryEditRoute,
    getStockListRoute,
    getStockDepositRoute,
    getStockTransferRoute,
    getStockCreateRoute,
    getStockEditRoute,
    redirectToCategoryList,
    redirectToRecipeList,
    redirectToStockList,
  }
}
