import React from 'react'
import { FormApi, IFormState } from 'react-form'

import { isFormValid } from '../../../helpers/form'
import { i18n } from '../../../i18n'
import { FormSubmit } from '../../../molecules/FormSubmit'

interface IProps {
  isCreate?: boolean
}

export const SubmitButton: React.FC<IProps> = ({ isCreate = false }) => (
  <FormApi
    render={form => (
      <FormSubmit id="form-submit">{getButtonText(form, isCreate)}</FormSubmit>
    )}
  />
)

function getButtonText(form: IFormState, isCreate: boolean) {
  if (!isFormValid(form)) {
    return i18n.t`Fix form errors`
  }
  return isCreate ? i18n.t`Create` : i18n.t`Save`
}
