import { observer } from 'mobx-react-lite'
import React from 'react'

import { useRoot } from '../../../atoms/RootProvider'
import { SelectControl } from '../../../atoms/SelectControl'
import { Sizer } from '../../../atoms/Sizer'
import {
  QMenuCategoryFilterCategories,
  useQMenuCategoryFilter,
} from '../../../graph/generated'
import { RM } from '../../../helpers/ramda'
import { i18n } from '../../../i18n'
import { getCategoryTypeLabel } from '../labels'
import { TRecipeType } from '../menutypes'

type TCategory = QMenuCategoryFilterCategories

export const MenuCategoryFilter = observer(() => {
  const {
    settings: {
      menu: { recipe },
    },
  } = useRoot()

  const { data, loading } = useQMenuCategoryFilter({
    branchId: recipe.branchId,
  })

  const getOptions = () => {
    if (!(data && data.categories)) {
      return []
    }
    const grouped = RM.groupBy(
      cat => cat.categoryType.enum.toString(),
      data.categories,
    )
    return Object.keys(grouped).map(typeString => ({
      label: getCategoryTypeLabel(typeString as TRecipeType),
      options: grouped[typeString],
    }))
  }

  return (
    <Sizer width={17}>
      <SelectControl<TCategory>
        key={recipe.branchId}
        onChange={value => {
          recipe.setCategoryFilter(value ? (value as TCategory).id : null)
        }}
        options={getOptions()}
        isMulti={false}
        isClearable={true}
        isSearchable={true}
        isLoading={loading}
        placeholder={i18n.t`Filter by category`}
        getOptionValue={RM.prop('id')}
        getOptionLabel={RM.propOr('', 'nameLabel')}
      />
    </Sizer>
  )
})
