import gql from 'graphql-tag'

import { buildQuery } from '../../../graph/buildQuery'
import { GVats } from '../../../graph/types'

export const VatsQuery = gql`
  query GVats($branchId: ID!) {
    vats(companyBranch: $branchId) {
      id
      vat
    }
  }
`

export const QueryVat = buildQuery<GVats.Query, GVats.Variables>(VatsQuery)
