import React from 'react'
import { Redirect, Route, Switch } from 'react-router-dom'

import { NotFoundPage } from '../../../pages/NotFoundPage'
import { SecureRoute } from '../../../routes/SecureRoute'
import { MENU_ROUTE, useMenuRouting } from '../hooks/useMenuRouting'
import { MenuCategoryListPage } from '../pages/MenuCategoryListPage'
import { MenuCategoryPage } from '../pages/MenuCategoryPage'
import { MenuRecipeListPage } from '../pages/MenuRecipeListPage'
import { MenuRecipePage } from '../pages/MenuRecipePage'
import { MenuStockDepositPage } from '../pages/MenuStockDepositPage'
import { MenuStockListPage } from '../pages/MenuStockListPage'
import { MenuStockPage } from '../pages/MenuStockPage'
import { MenuStockTransferPage } from '../pages/MenuStockTransferPage'

interface IProps {}

export const MenuRoutes: React.FC<IProps> = () => {
  const routing = useMenuRouting()
  return (
    <Switch>
      <SecureRoute
        path={routing.getCategoryEditRoute(':categoryId')}
        component={MenuCategoryPage}
      />
      <SecureRoute
        path={routing.getCategoryListRoute()}
        component={MenuCategoryListPage}
      />
      <SecureRoute
        path={routing.getRecipeEditRoute(':recipeId')}
        component={MenuRecipePage}
      />
      <SecureRoute
        path={routing.getRecipeListRoute()}
        component={MenuRecipeListPage}
      />
      <SecureRoute
        path={routing.getStockDepositRoute()}
        component={MenuStockDepositPage}
      />
      <SecureRoute
        path={routing.getStockTransferRoute()}
        component={MenuStockTransferPage}
      />
      <SecureRoute
        path={routing.getStockEditRoute(':stockId')}
        component={MenuStockPage}
      />
      <SecureRoute
        path={routing.getStockListRoute()}
        component={MenuStockListPage}
      />
      <Redirect to={routing.getCategoryListRoute()} from={MENU_ROUTE} />
      <Route component={NotFoundPage} />
    </Switch>
  )
}

export default MenuRoutes
