import React from 'react'
import validator from 'validator'

import {
  Column,
  ColumnRendererRemove,
  DataTable,
  DataTableEmptyText,
} from '../../../atoms/DataTable'
import { FormInputTextBox } from '../../../atoms/FormInputTextBox'
import { Sizer } from '../../../atoms/Sizer'
import { i18n } from '../../../i18n'
import { Spacing } from '../../../styling'
import { TIngredient } from '../menutypes'

interface IProps {
  ingredients: TIngredient[]
  onRemove?(stockItemId: ID): void
  isLoading?: boolean
}

export const RecipeIngredientsTable: React.FC<IProps> = ({
  ingredients,
  onRemove,
  isLoading = false,
}) => (
  <DataTable
    isLoading={isLoading}
    items={ingredients}
    disableHeader={true}
    noRowsRenderer={() => (
      <DataTableEmptyText text={i18n.t`Recipe has no ingredients`} />
    )}
  >
    <Column
      dataKey="stockItem"
      width={Spacing.ButtonColumnWidthPx}
      minWidth={Spacing.ButtonColumnWidthPx}
      cellRenderer={ColumnRendererRemove}
      columnData={{ onRemove }}
    />
    <Column dataKey="label" width={300} label={i18n.t`Name`} />
    <Column
      dataKey="quantity"
      width={60}
      label={i18n.t`Quantity`}
      cellRenderer={({ rowIndex }) => (
        <Sizer width={2.5}>
          <FormInputTextBox
            field={['ingredients', rowIndex, 'quantity']}
            validate={validateQuantity}
            inputFullWidth={true}
          />
        </Sizer>
      )}
    />
    <Column dataKey="unitLabel" width={100} label={i18n.t`Ware unit`} />
  </DataTable>
)

function validateQuantity(value: string) {
  const fieldName = i18n.t`Quantity`
  if (validator.isEmpty(value + '')) {
    return i18n.t`${fieldName} must be entered`
  }
  if (!validator.isFloat(value + '', { min: 0 })) {
    return i18n.t`${fieldName} must be a positive number`
  }
  return null
}
