import { css } from 'emotion'
import { useObserver } from 'mobx-react-lite'
import React from 'react'
import { FieldType } from 'react-form'

import { FormInputFlatSelect } from '../../../atoms/FormInputFlatSelect'
import { IconLine } from '../../../atoms/IconLine'
import { useRoot } from '../../../atoms/RootProvider'
import { i18n } from '../../../i18n'
import { QueryVat } from './FormInputVatSelect.graphql'

interface IProps {
  field?: FieldType
}

const container = css`
  margin-left: 5rem;
`

export const FormInputVatSelect: React.FC<IProps> = ({
  field = 'price.vat',
}) => {
  const { settings } = useRoot()

  return useObserver(() => (
    <QueryVat
      variables={{
        branchId: settings.branchId,
      }}
      fetchPolicy="cache-and-network"
      renderLoading={false}
      render={({ vats }) => (
        <IconLine className={container} label={`${i18n.t`VAT`}:`}>
          {vats && (
            <FormInputFlatSelect
              field={field}
              items={vats}
              formatItem={({ vat }) => `${vat}%`}
              isRequired={true}
              validationName={i18n.t`VAT`}
            />
          )}
        </IconLine>
      )}
    />
  ))
}
