import { RemoveRedEye } from '@material-ui/icons'
import React from 'react'

import { SizerButton } from '../atoms/SizerButton'
import { ITouchButtonProps, TouchButton } from '../molecules/TouchButton'
import { Colors } from '../styling'

interface IProps extends ITouchButtonProps {
  isVisible: boolean
}

export const TouchButtonVisibility: React.FC<IProps> = ({
  isVisible,
  ...props
}) => (
  <SizerButton>
    <TouchButton
      icon={RemoveRedEye}
      iconColor={isVisible ? Colors.white : Colors.greyMiddle}
      color={Colors.greyMiddle}
      {...props}
    />
  </SizerButton>
)
