import { Toll } from '@material-ui/icons'
import React from 'react'
import { isEmpty } from 'validator'

import {
  FormInputTextBox,
  TFormInputTextBoxProps,
} from '../../../atoms/FormInputTextBox'
import { IconLine } from '../../../atoms/IconLine'
import { Sizer } from '../../../atoms/Sizer'
import { i18n } from '../../../i18n'
import { validateNumber } from '../../../locale/numeral'

interface IProps extends Partial<TFormInputTextBoxProps> {}

const formInputPriceValidate = (value: string) => {
  if (
    value === undefined ||
    isEmpty(value + '') ||
    !validateNumber(value + '')
  ) {
    return i18n.t`Price must be a number`
  }
  return null
}

export const FormInputPrice: React.FC<IProps> = ({
  field = 'price.value',
  ...props
}) => (
  <IconLine icon={Toll}>
    <Sizer width={5}>
      <FormInputTextBox
        field={field}
        inputFullWidth={true}
        validate={formInputPriceValidate}
        {...props}
      />
    </Sizer>
  </IconLine>
)
