import React from 'react'
import { Field, IFieldApi } from 'react-form'
import {
  arrayMove,
  SortableContainer,
  SortableElement,
  SortEndHandler,
} from 'react-sortable-hoc'
import styled from 'styled-components'

import { DataList } from '../../../atoms/DataList'
import { DragHandle } from '../../../atoms/DragHandle'
import { NoteText } from '../../../atoms/NoteText'
import { WareCategoryTypeEnum } from '../../../graph/generated'
import { GMenuRecipe } from '../../../graph/types'
import { RM } from '../../../helpers/ramda'
import { i18n } from '../../../i18n'
import { TouchButtonRemove } from '../../../molecules/TouchButtonRemove'
import { Spacing } from '../../../styling'
import { priceTypes } from './RecipeFormPricing'
import { RecipeSuggestionPanel } from './RecipeSuggestionPanel'

interface IProps {
  categoryType: WareCategoryTypeEnum
}

const Container = styled.div`
  display: flex;
  flex-direction: column;
  height: calc(100% - 2rem);
  width: 100%;
`

const FormFixedRow = styled.div`
  flex: 0 0 auto;
`

const FormStretchedRow = styled.div`
  flex: 1 1 0;
  margin: 1rem 0 0 1rem;
`

const SortableRecipes = SortableContainer(FormStretchedRow)

const RecipeRow = styled.div`
  display: grid;
  grid-gap: 1rem;
  grid-template-columns: ${Spacing.ButtonColumnWidthPx}px 100px 3fr 1fr 1fr 50px;
`

const MoveCell = styled.div`
  cursor: move;
`

type TRecipe = GMenuRecipe.Fragment

interface IRecipeRow {
  recipe: TRecipe
  onRemove(): void
}

const SortableRecipe = SortableElement<IRecipeRow>(({ recipe, onRemove }) => {
  const prices = priceTypes.map(({ priceType, label }) => {
    const foundPrice = recipe.prices.find(
      RM.pathEq(['priceType', 'enum'], priceType),
    )
    return (
      <MoveCell key={priceType}>
        <NoteText>{i18n._(label)}</NoteText>
        {foundPrice ? (
          foundPrice.price.formattedValue
        ) : (
          <i>{i18n.t`Not set`.toLocaleLowerCase()}</i>
        )}
      </MoveCell>
    )
  })

  return (
    <RecipeRow>
      <div>
        <TouchButtonRemove onClick={onRemove} />
      </div>
      <MoveCell>{recipe.code}</MoveCell>
      <MoveCell>
        {recipe.nameLabel}
        <NoteText>{recipe.internalNote}</NoteText>
      </MoveCell>
      {prices}
      <DragHandle />
    </RecipeRow>
  )
})

export const CategoryRecipesView: React.FC<IProps> = ({ categoryType }) => {
  return (
    <Field field="recipes">
      {(fieldApi: IFieldApi<ReadonlyArray<TRecipe>>) => {
        const recipes = fieldApi.value
        const onHandleSorting: SortEndHandler = async ({
          newIndex,
          oldIndex,
        }) => {
          if (newIndex === oldIndex) {
            return
          }
          const sortedItems = arrayMove(
            recipes as TRecipe[],
            oldIndex,
            newIndex,
          )

          fieldApi.setValue(sortedItems)
        }
        return (
          <Container>
            <FormFixedRow>
              <RecipeSuggestionPanel
                categoryType={categoryType}
                filterRecipes={RM.without(fieldApi.value)}
                onAdd={addedRecipe =>
                  fieldApi.setValue(fieldApi.value.concat(addedRecipe))
                }
              />
            </FormFixedRow>
            <SortableRecipes
              lockAxis="y"
              lockToContainerEdges={true}
              onSortEnd={onHandleSorting}
            >
              <DataList
                items={fieldApi.value}
                heightStretch={true}
                rowHeight={60}
                renderItem={(recipe, index) => (
                  <SortableRecipe
                    recipe={recipe}
                    onRemove={() => {
                      fieldApi.setValue(
                        RM.reject(RM.propEq('id', recipe.id), fieldApi.value),
                      )
                    }}
                    index={index}
                  />
                )}
                rowCount={fieldApi.value.length}
              />
            </SortableRecipes>
          </Container>
        )
      }}
    </Field>
  )
}
