import React from 'react'
import styled, { css } from 'styled-components'

import { AllergenEnum } from '../../../graph/generated'
import { Colors, Spacing } from '../../../styling'
import { QueryAllergens } from '../molecules/FormInputAllergens.graphql'

interface IProps {
  selected: AllergenEnum[]
  onToggle?(allergen: AllergenEnum): void
}

const AllergenBox = styled.div`
  float: left;
  width: 2.3rem;
  height: 2.3rem;
  border: 1px solid ${Colors.greyMiddle};
  margin: 0.3rem 0.6rem 0.3rem ${Spacing.MarginAroundFormComponents};
  font-size: 1.5rem;
  text-align: center;
  vertical-align: middle;
  cursor: default;
  ${({ active }: { active: boolean }) => css`
    color: ${active ? Colors.black : Colors.greyMiddle};
    background-color: ${active ? Colors.yellow : Colors.white};
  `};
`

export const MenuAllergenIndicator: React.FC<IProps> = ({
  selected,
  onToggle,
}) => (
  <QueryAllergens
    render={({ allergens }) => (
      <>
        {allergens.map(allergen => {
          const props = {
            active: selected.includes(allergen.enum),
            onClick: onToggle && (() => onToggle(allergen.enum)),
          }
          return (
            <AllergenBox key={allergen.enum} title={allergen.code} {...props}>
              {allergen.category}
            </AllergenBox>
          )
        })}
      </>
    )}
  />
)
