import { Trans } from '@lingui/macro'
import { RM } from '@speedlo/tools'
import { Observer, observer } from 'mobx-react-lite'
import React from 'react'
import { TableCellProps } from 'react-virtualized'
import styled from 'styled-components'

import {
  checkColumnAccess,
  Column,
  ColumnRendererEdit,
  DataTable,
} from '../../../atoms/DataTable'
import { Loading } from '../../../atoms/Loading'
import { Sizer } from '../../../atoms/Sizer'
import { QMenuStockListStockItems } from '../../../graph/generated'
import { isPartialMatch } from '../../../helpers/filter'
import { i18n } from '../../../i18n'
import { Spacing } from '../../../styling'
import { ImportCheckbox } from '../atoms/ImportCheckbox'
import { useMenu } from '../hooks/useMenu'
import { useTransferModel } from '../hooks/useTransferModel'
import { StockTransferAmountInput } from '../molecules/StockTransferAmountInput'

type TStockItem = QMenuStockListStockItems

interface IProps {
  stockItems: RoA<TStockItem>
  isLoading: boolean
  isTransferTable: boolean
}

const ANIMATION_DURATION = '0.3s'
const ANIMATED_COLUMN_WIDTH = 130

const StyledDataTable = styled(DataTable)`
  [aria-label='grid']:focus {
    outline: none;
  }

  [role='columnheader'] {
    display: flex;
    align-items: center;
    overflow: hidden;
  }

  .flex-animation,
  [role='columnheader'] {
    transition: flex ${ANIMATION_DURATION};
  }
`

const VerticallyCenteredFlexContainer = styled.div`
  display: flex;
  align-items: center;
`

export const StockListTable: React.FC<IProps> = observer(
  ({ stockItems, isLoading, isTransferTable }) => {
    const { stock } = useMenu()
    const transfer = useTransferModel()

    const getItemMaxLimit = ({ quantity, allowNegative }: TStockItem) => {
      if (allowNegative) {
        return Number.MAX_SAFE_INTEGER
      } else {
        return Math.max(quantity, 0)
      }
    }

    React.useEffect(() => {
      if (isTransferTable) {
        const unfilteredItems = stockItems.map(item => {
          const max = getItemMaxLimit(item)
          return { id: item.id, max }
        })

        transfer.initItems(unfilteredItems)
      }
    }, [isTransferTable, stockItems, transfer])

    const searchValue = stock.filterInput
    const partialMatch = isPartialMatch(searchValue)
    const filterItems = RM.cond([
      [RM.propEq('plu', searchValue), RM.T],
      [RM.propSatisfies(partialMatch, 'code'), RM.T],
      [RM.propSatisfies(partialMatch, 'nameLabel'), RM.T],
    ])

    const renderImportItemCheckbox = React.useCallback(
      ({ cellData: itemId }: TableCellProps) => (
        <Observer>
          {() => (
            <ImportCheckbox
              checked={transfer.isImportingItem(itemId)}
              onChange={() => transfer.toggleImportItem(itemId)}
            />
          )}
        </Observer>
      ),
      [transfer],
    )

    const renderImportAllHeader = React.useCallback(
      () => (
        <Observer>
          {() => (
            <ImportCheckbox
              checked={transfer.isImportingAll}
              onChange={transfer.toggleImportAll}
              sideText={<Trans>Import</Trans>}
            />
          )}
        </Observer>
      ),
      [transfer],
    )

    const renderAmountInput = React.useCallback(
      ({ cellData }: TableCellProps) => {
        const itemId = cellData as ID
        return (
          <Observer>
            {() => (
              <VerticallyCenteredFlexContainer>
                <Sizer width={5.5} margin="0 0.5rem 0 0">
                  <StockTransferAmountInput stockItemId={itemId} />
                </Sizer>
              </VerticallyCenteredFlexContainer>
            )}
          </Observer>
        )
      },
      [],
    )

    const quantityColumn = (
      <Column
        dataKey="quantity"
        width={100}
        label={i18n.t`Card stock`}
        cellRenderer={stockWithUnit}
      />
    )

    return (
      <StyledDataTable
        items={stockItems.filter(filterItems)}
        heightStretch={true}
        noRowsRenderer={() =>
          isLoading ? (
            <Loading />
          ) : (
            <h3>
              <Trans>Selected branch has no stock cards</Trans>
            </h3>
          )
        }
      >
        {isTransferTable && (
          <Column
            dataKey="id"
            width={transfer.isImportMode ? ANIMATED_COLUMN_WIDTH : 0}
            label={i18n.t`Import`}
            headerRenderer={renderImportAllHeader}
            cellRenderer={renderImportItemCheckbox}
            className="flex-animation"
          />
        )}
        {isTransferTable && (
          <Column
            dataKey="id"
            width={transfer.isTransferMode ? ANIMATED_COLUMN_WIDTH : 0}
            label={i18n.t`Amount`}
            cellRenderer={renderAmountInput}
            className="flex-animation"
          />
        )}
        {!isTransferTable && (
          <Column
            dataKey="id"
            width={Spacing.ButtonColumnWidthPx}
            minWidth={Spacing.ButtonColumnWidthPx}
            cellRenderer={checkColumnAccess(
              { canQuery: 'stockItem' },
              ColumnRendererEdit,
            )}
          />
        )}
        {isTransferTable && quantityColumn}
        <Column dataKey="code" width={100} label={i18n.t`Card code`} />
        <Column
          dataKey="nameLabel"
          width={300}
          minWidth={30}
          flexGrow={2}
          label={i18n.t`Card name`}
        />
        {!isTransferTable && quantityColumn}
        <Column
          dataKey="allowNegative"
          width={300}
          label={i18n.t`Negative stock`}
          cellRenderer={stockAllowNegative}
        />
        {isTransferTable ? null : (
          <Column dataKey="note" width={300} label={i18n.t`Card note`} />
        )}
      </StyledDataTable>
    )
  },
)

function stockWithUnit({ cellData, rowData }: TableCellProps) {
  return `${cellData} ${rowData.wareUnit.code}`
}

function stockAllowNegative({ cellData }: TableCellProps) {
  return cellData === true ? <Trans>Permitted</Trans> : <Trans>Forbidden</Trans>
}
