import React from 'react'

import {
  FormInputTextBox,
  TFormInputTextBoxProps,
} from '../../../atoms/FormInputTextBox'
import { IconLine } from '../../../atoms/IconLine'
import { Sizer } from '../../../atoms/Sizer'
import { i18n } from '../../../i18n'

interface IProps extends Partial<TFormInputTextBoxProps> {
  icon?: string
  isRequired?: boolean
}

export const FormInputCode: React.FC<IProps> = ({
  field = 'code',
  icon = 'label_outline',
  isRequired = true,
  ...props
}) => (
  <IconLine label={i18n.t`Code`.toLocaleLowerCase()} labelWidth={7}>
    <Sizer width={9}>
      <FormInputTextBox
        field={field}
        inputFullWidth={true}
        isRequired={true}
        validatioName={i18n.t`Code`}
        {...props}
      />
    </Sizer>
  </IconLine>
)
