import { observer } from 'mobx-react-lite'
import React from 'react'
import { Field, IFieldApi } from 'react-form'

import { useRoot } from '../../../atoms/RootProvider'
import { useQMenuStockList } from '../../../graph/generated'
import { GMenuStockItem } from '../../../graph/types'
import { RM } from '../../../helpers/ramda'
import { i18n } from '../../../i18n'
import { MenuSuggestionPanel } from '../atoms/MenuSuggestionPanel'
import { TIngredient } from '../menutypes'
import { RecipeIngredientsTable } from '../molecules/RecipeIngredientsTable'

interface IProps {
  ingredients: TIngredient[]
  onAdd(ingredient: TIngredient): void
  onRemove(stockItemId: ID): void
}

const unselectedStockOnly = RM.differenceWith(
  ({ id }: GMenuStockItem.Fragment, { stockItem }: TIngredient) =>
    id === stockItem,
)

export const RecipeIngredientsControl = observer<IProps>(
  ({ ingredients, onAdd, onRemove }) => {
    const { settings } = useRoot()
    const { data, loading } = useQMenuStockList({
      branchId: settings.branchId,
    })

    return (
      <>
        <RecipeIngredientsTable ingredients={ingredients} onRemove={onRemove} />
        <MenuSuggestionPanel<GMenuStockItem.Fragment>
          isLoading={loading}
          addButtonText={i18n.t`Add ingredient`}
          options={
            loading ? [] : unselectedStockOnly(data.stockItems, ingredients)
          }
          onAdd={(addedStockItem: GMenuStockItem.Fragment) => {
            onAdd({
              stockItem: addedStockItem.id,
              label: addedStockItem.nameLabel,
              quantity: 1,
              unitLabel: addedStockItem.wareUnit.code,
              allergens: addedStockItem.allergens.map(RM.prop('enum')),
            })
          }}
        />
      </>
    )
  },
)

export const RecipeIngredientsView = () => {
  return (
    <Field field="ingredients">
      {(fieldApi: IFieldApi<TIngredient[]>) => (
        <RecipeIngredientsControl
          ingredients={fieldApi.value}
          onAdd={ingredient => {
            fieldApi.setValue([...fieldApi.value, ingredient])
          }}
          onRemove={stockItemId => {
            fieldApi.setValue(
              RM.reject(RM.propEq('stockItem', stockItemId), fieldApi.value),
            )
          }}
        />
      )}
    </Field>
  )
}
