import { ErrorOutline } from '@material-ui/icons'
import React from 'react'
import { Field, FieldType } from 'react-form'

import { IconLine } from '../../../atoms/IconLine'
import { AllergenEnum } from '../../../graph/generated'
import { RM } from '../../../helpers/ramda'
import { MenuAllergenIndicator } from '../atoms/AllergenIndicator'

interface IProps {
  field?: FieldType
}

export const FormInputAllergens: React.FC<IProps> = ({
  field = 'allergens',
}) => (
  <Field field={field}>
    {fieldApi => {
      const onToggle = (allergen: AllergenEnum) => {
        const current: AllergenEnum[] = fieldApi.value
        if (current.includes(allergen)) {
          fieldApi.setValue(RM.without([allergen], current))
        } else {
          fieldApi.setValue([...current, allergen])
        }
      }
      return (
        <IconLine icon={ErrorOutline} labelWidth={7}>
          <MenuAllergenIndicator
            selected={fieldApi.value}
            onToggle={onToggle}
          />
        </IconLine>
      )
    }}
  </Field>
)
