import { i18nMark } from '@lingui/core'

import { WareCategoryTypeEnum } from '../../graph/generated'
import { i18n } from '../../i18n'
import { TCategoryType, TRecipeType } from './menutypes'

const categories = {
  [WareCategoryTypeEnum.Main]: i18nMark('Ware categories'),
  [WareCategoryTypeEnum.SideDish]: i18nMark('Sidedish categories'),
  [WareCategoryTypeEnum.Cover]: i18nMark('Cover categories'),
}

const recipes = {
  [WareCategoryTypeEnum.Main]: i18nMark('Ware recipe'),
  [WareCategoryTypeEnum.SideDish]: i18nMark('Sidedish recipe'),
  [WareCategoryTypeEnum.Cover]: i18nMark('Cover recipe'),
}

export function getCategoryTypeLabel(categoryType: TCategoryType) {
  return i18n._(categories[categoryType])
}

export function getRecipeTypeLabel(recipeType: TRecipeType) {
  return i18n._(recipes[recipeType])
}
