import _ from 'lodash'
import React from 'react'
import { Field, IFieldApi } from 'react-form'

import { RM } from '../../../helpers/ramda'
import { MenuAllergenIndicator } from '../atoms/AllergenIndicator'
import { TIngredient } from '../menutypes'

export const RecipeAllergenDisplay: React.FC = () => (
  <Field field="ingredients">
    {({ value }: IFieldApi<TIngredient[]>) => {
      const allergens = _.uniq(_.flatten(value.map(RM.prop('allergens'))))
      return <MenuAllergenIndicator selected={allergens} />
    }}
  </Field>
)
