import gql from 'graphql-tag'

import { buildQuery } from '../../../graph/buildQuery'
import { GWareUnits } from '../../../graph/types'

export const WareUnitsQuery = gql`
  query GWareUnits {
    wareUnits {
      id
      code
      enum
    }
  }
`

export const QueryWareUnits = buildQuery<
  GWareUnits.Query,
  GWareUnits.Variables
>(WareUnitsQuery)
