import React from 'react'

import { WarePriceTypeEnum } from '../../../graph/generated'
import { GMenuRecipeForm } from '../../../graph/types'
import { RM } from '../../../helpers/ramda'
import { MenuBaseFormLoader } from '../atoms/MenuBaseFormLoader'
import { useMenu } from '../hooks/useMenu'
import { TRecipeFormValues, TRecipeType } from '../menutypes'
import { MenuRecipeFormQuery } from './RecipeForm.graphql'

interface IProps {
  isCreate: boolean
  recipeId: ID
  recipeType: TRecipeType
}

export const RecipeFormLoader: React.FC<IProps> = ({
  isCreate,
  recipeId,
  recipeType,
  children,
}) => {
  const menu = useMenu()
  return (
    <MenuBaseFormLoader<TRecipeFormValues>
      isCreate={isCreate}
      onCreate={values => menu.recipe.createRecipe(values, recipeType)}
      onUpdate={values => menu.recipe.updateRecipe(values)}
      getDefaultValues={() => getDefaults(recipeType)}
      loadDefaults={async () => {
        const { vats, currencyCode } = await menu.recipe.queryDefaults()
        const defaults = { ...getDefaults(recipeType) }
        defaults.prices = getPriceMap(priceType => ({
          value: 0,
          vatId: vats[priceType],
          currencyCode,
        }))
        return defaults
      }}
      loadValues={async () => {
        const { data } = await menu.client.query<GMenuRecipeForm.Query>({
          query: MenuRecipeFormQuery,
          variables: { recipeId },
          fetchPolicy: 'no-cache',
        })
        return normalizeValues(data.recipe)
      }}
    >
      {children}
    </MenuBaseFormLoader>
  )
}

function getPriceMap<T>(acc: (priceType: WarePriceTypeEnum) => T) {
  return [WarePriceTypeEnum.Delivery, WarePriceTypeEnum.Inhouse].reduce(
    (result, priceType) => {
      result[priceType] = acc(priceType)
      return result
    },
    {},
  ) as { [priceType in WarePriceTypeEnum]: T }
}

function getDefaults(recipeType: TRecipeType): TRecipeFormValues {
  return {
    id: 'NEW',
    code: '',
    nameLabel: '',
    internalNote: '',
    descriptionLabel: '',
    image: '',
    visible: true,
    recipeType: {
      enum: recipeType,
    },
    sourcePrices: [],
    prices: getPriceMap(() => ({
      value: 0,
      vatId: null,
      currencyCode: '',
    })),
    sourceAssociatedRecipes: [],
    associatedRecipes: [],
    sourceIngredients: [],
    ingredients: [],
    sideDishes: [],
    categories: [],
  }
}

function normalizeValues(
  recipeSource: GMenuRecipeForm.Recipe,
): TRecipeFormValues {
  return {
    ...recipeSource,
    associatedRecipes: recipeSource.sourceAssociatedRecipes.map(
      ({ priceType, recipe }) => ({
        warePriceType: priceType.enum,
        nameLabel: recipe.nameLabel!,
        associatedRecipe: recipe.id,
        formattedPrice: '?',
      }),
    ),
    ingredients: recipeSource.sourceIngredients.map(
      ({ quantity, stockItem: { id, label, allergens, wareUnit } }) => ({
        stockItem: id,
        label,
        quantity,
        unitLabel: wareUnit.code,
        allergens: allergens.map(RM.prop('enum')),
      }),
    ),
    prices: getPriceMap(priceType => {
      const sourcePrice = recipeSource.sourcePrices.find(
        RM.pathEq(['priceType', 'enum'], priceType),
      )
      if (!sourcePrice) {
        return {
          value: 0,
          vatId: null,
          currencyCode: '',
        }
      }
      return {
        value: sourcePrice.price.value,
        vatId: sourcePrice.vat.id,
        currencyCode: sourcePrice.price.currency.code,
      }
    }),
  }
}
