import { observer } from 'mobx-react-lite'
import React from 'react'

import { useRoot } from '../../../atoms/RootProvider'
import { useQMenuStockList } from '../../../graph/generated'
import { StockListTable } from './StockListTable'

interface IProps {
  isTransferring?: boolean
}

export const StockListLoader = observer<IProps>(
  ({ isTransferring = false }) => {
    const { settings } = useRoot()
    const { data, loading } = useQMenuStockList({
      branchId: settings.branchId,
    })

    const stockItems =
      !data || !data.stockItems || data.stockItems.length === 0
        ? []
        : data.stockItems

    return (
      <StockListTable
        stockItems={stockItems}
        isLoading={loading}
        isTransferTable={isTransferring}
      />
    )
  },
)
