import { Edit } from '@material-ui/icons'
import { LocationDescriptor } from 'history'
import React, { ComponentType } from 'react'

import { RelativeLink } from '../atoms/RelativeLink'
import { SizerButton } from '../atoms/SizerButton'
import { ITouchButtonProps, TouchButton } from './TouchButton'

interface IProps extends LegacyOmit<ITouchButtonProps, 'onClick'> {
  target: LocationDescriptor
  Link?: ComponentType<{ to: LocationDescriptor }>
}

export const TouchButtonEdit: React.FC<IProps> = ({
  target,
  Link = RelativeLink,
  ...props
}) => (
  <Link to={target}>
    <SizerButton>
      <TouchButton icon={Edit} {...props} />
    </SizerButton>
  </Link>
)
