import { ErrorOutline } from '@material-ui/icons'
import React from 'react'

import { CardDock } from '../../../atoms/CardDock'
import { FormColumn, FormContainer } from '../../../atoms/FormContainer'
import { FormInputCheckbox } from '../../../atoms/FormInputCheckbox'
import { IconLine } from '../../../atoms/IconLine'
import { WareCategoryTypeEnum } from '../../../graph/generated'
import { i18n } from '../../../i18n'
import { WithSettings } from '../../settings/atoms/WithSettings'
import { IsCategoryType } from '../atoms/IsCategoryType'
import { TRecipeType } from '../menutypes'
import { FormDeleteButton } from '../molecules/FormDeleteButton'
import { FormInputCode } from '../molecules/FormInputCode'
import { FormInputDescription } from '../molecules/FormInputDescription'
import { FormInputInternalNote } from '../molecules/FormInputInternalNote'
import { FormInputName } from '../molecules/FormInputName'
import { RecipeAllergenDisplay } from '../molecules/RecipeAllergenDisplay'

interface IProps {
  recipeType: TRecipeType
}

export const RecipeFormBasic: React.FC<IProps> = ({ recipeType }) => {
  const isMain = recipeType === WareCategoryTypeEnum.Main
  return (
    <FormContainer>
      <FormColumn>
        <FormInputName placeholder={i18n.t`Recipe name`} />
        <FormInputCode placeholder={i18n.t`Recipe code`} />
        <FormInputInternalNote placeholder={i18n.t`Recipe internal note`} />
        {isMain && (
          <FormInputDescription
            placeholder={i18n.t`Public note for customers, allergens`}
          />
        )}
        <IsCategoryType type={recipeType} isMain={true}>
          <IconLine labelWidth={7} icon={ErrorOutline}>
            {i18n.t`Allergens based on stock cards`}
            <br />
            <RecipeAllergenDisplay />
          </IconLine>
        </IsCategoryType>
        <IconLine labelWidth={7}>
          <FormInputCheckbox
            field="visible"
            label={i18n.t`To show this recipe`}
          />
        </IconLine>
      </FormColumn>
      <CardDock position="BottomRight">
        <WithSettings
          render={({ menu }) => (
            <FormDeleteButton
              confirmText={i18n.t`Remove this recipe?`}
              onDelete={id => menu.recipe.deleteRecipe(id)}
            />
          )}
        />
      </CardDock>
    </FormContainer>
  )
}
