import { AddCircle } from '@material-ui/icons'
import { LocationDescriptor } from 'history'
import React, { MouseEvent } from 'react'
import { Link } from 'react-router-dom'
import styled from 'styled-components'

import { SizerButton } from '../../../atoms/SizerButton'
import { TouchButton } from '../../../molecules/TouchButton'
import { Colors } from '../../../styling'

interface IProps {
  routeTo?: LocationDescriptor
  onClick?: (event: MouseEvent<HTMLButtonElement>) => void
}

const CreateButtonContainer = styled.div`
  position: absolute;
  right: 0;
  z-index: 5;
`

export const CreateButton: React.FC<IProps> = ({ routeTo, onClick }) => {
  const button = (
    <SizerButton>
      <TouchButton icon={AddCircle} color={Colors.green} onClick={onClick} />
    </SizerButton>
  )

  return (
    <CreateButtonContainer>
      {routeTo ? <Link to={routeTo}>{button}</Link> : button}
    </CreateButtonContainer>
  )
}
