import { i18nMark } from '@lingui/core'
import React, { Fragment } from 'react'

import { VerticalGap } from '../../../atoms/VerticalGap'
import { WarePriceTypeEnum } from '../../../graph/generated'
import { i18n } from '../../../i18n'
import { TRecipeType } from '../menutypes'
import { FormPricingField } from '../molecules/FormPricingField'
import { RecipeAssociatedView } from './RecipeAssociatedView'

interface IProps {
  recipeId: MaybeID
  recipeType: TRecipeType
}

export const priceTypes = [
  {
    priceType: WarePriceTypeEnum.Delivery,
    label: i18nMark('Delivery sale'),
  },
  {
    priceType: WarePriceTypeEnum.Inhouse,
    label: i18nMark('Local sale'),
  },
]

export const RecipeFormPricing: React.FC<IProps> = ({
  recipeType,
  recipeId,
}) => (
  <>
    {priceTypes.map(({ priceType, label }) => (
      <Fragment key={priceType}>
        <FormPricingField priceType={priceType} label={i18n._(label)} />
        <VerticalGap />
        {recipeType === TRecipeType.Main && (
          <RecipeAssociatedView priceType={priceType} recipeId={recipeId} />
        )}
      </Fragment>
    ))}
  </>
)
