import { Search } from '@material-ui/icons'
import { useDebounceCallback } from '@speedlo/hooks'
import React from 'react'

import { FormInputStyled } from '../../../atoms/FormInputStyled'
import { IconLine } from '../../../atoms/IconLine'
import { useRoot } from '../../../atoms/RootProvider'
import { Sizer } from '../../../atoms/Sizer'
import { i18n } from '../../../i18n'

interface IProps {
  modelType: 'recipe' | 'category' | 'stock'
}

export const MenuFilterInput: React.FC<IProps> = ({ modelType }) => {
  const {
    settings: { menu },
  } = useRoot()

  const model = menu[modelType]
  const onChange = useDebounceCallback(model.setFilterInput)

  return (
    <IconLine icon={Search}>
      <Sizer width={17}>
        <FormInputStyled
          defaultValue={model.filterInput}
          inputFullWidth={true}
          onChange={ev => onChange(ev.target.value)}
          placeholder={i18n.t`Filter by name or code`}
        />
      </Sizer>
    </IconLine>
  )
}
