import { observer } from 'mobx-react-lite'
import React from 'react'

import { useRoot } from '../../../atoms/RootProvider'
import {
  useQMenuRecipeList,
  WareCategoryTypeEnum,
} from '../../../graph/generated'
import { GMenuRecipe } from '../../../graph/types'
import { MenuSuggestionPanel } from '../atoms/MenuSuggestionPanel'

type TRecipe = GMenuRecipe.Fragment

interface IProps {
  categoryType?: WareCategoryTypeEnum
  onAdd(recipe: TRecipe): void
  filterRecipes(recipes: ReadonlyArray<TRecipe>): TRecipe[]
}

export const RecipeSuggestionPanel = observer<IProps>(
  ({ categoryType, filterRecipes, onAdd }) => {
    const { settings } = useRoot()
    const { data, loading } = useQMenuRecipeList({
      branchId: settings.branchId,
      categoryType,
    })
    return (
      <MenuSuggestionPanel<TRecipe>
        isLoading={loading}
        options={loading ? [] : filterRecipes(data.recipes)}
        onAdd={onAdd}
      />
    )
  },
)
