import React from 'react'

import { WareCategoryTypeEnum } from '../../../graph/generated'

interface IProps {
  type: WareCategoryTypeEnum
  isMain?: boolean
  isSideDish?: boolean
  isCover?: boolean
  children: ReactNode | ReactNode[]
}

export const IsCategoryType: React.FC<IProps> = ({
  type,
  isMain,
  isSideDish,
  isCover,
  children,
}) => {
  if (
    (isMain === true && type === WareCategoryTypeEnum.Main) ||
    (isSideDish === true && type === WareCategoryTypeEnum.SideDish) ||
    (isCover === true && type === WareCategoryTypeEnum.Cover)
  ) {
    return <>{children}</>
  }
  return null
}
