import { AllergenEnum, WarePriceTypeEnum } from '../../graph/generated'
import { GMenuRecipeForm, GMenuRecipePrice } from '../../graph/types'

export {
  WareCategoryTypeEnum as TCategoryType,
  WareCategoryTypeEnum as TRecipeType,
  WarePriceTypeEnum as TPriceType,
} from '../../graph/generated'

export type TAssocRecipe = {
  warePriceType: WarePriceTypeEnum
  associatedRecipe: ID
  nameLabel?: string
  formattedPrice?: string
}

export type TIngredient = {
  stockItem: ID
  label: string
  quantity: number | string
  unitLabel: string
  allergens: AllergenEnum[]
}

export type TPricing = GMenuRecipePrice.Fragment
export type TRecipeFormValues = GMenuRecipeForm.Recipe & {
  associatedRecipes: TAssocRecipe[]
  ingredients: TIngredient[]
  prices: {
    [priceType in WarePriceTypeEnum]: {
      value: number
      vatId: MaybeID
      currencyCode: string
    }
  }
}
