import { SelectAll } from '@material-ui/icons'
import React from 'react'

import {
  FormInputSelect,
  IFormInputSelectProps,
} from '../../../atoms/FormInputSelect'
import { IconLine } from '../../../atoms/IconLine'
import { GWareUnits } from '../../../graph/types'
import { RM } from '../../../helpers/ramda'
import { i18n } from '../../../i18n'
import { QueryWareUnits } from './FormInputWareUnit.graphql'

interface IProps extends Partial<IFormInputSelectProps<GWareUnits.WareUnits>> {}

export const FormInputWareUnit: React.FC<IProps> = ({
  field = 'wareUnit',
  ...selectProps
}) => (
  <QueryWareUnits
    renderLoading={false}
    render={({ wareUnits }, { loading }) => (
      <IconLine icon={SelectAll} labelWidth={7}>
        <FormInputSelect<GWareUnits.WareUnits>
          field={field}
          options={(wareUnits as GWareUnits.WareUnits[]) || []}
          isLoading={loading}
          isRequired={true}
          validatioName={i18n.t`Ware unit`}
          getOptionLabel={RM.prop('code')}
          getOptionValue={RM.prop('enum')}
          width={12}
          {...selectProps}
        />
      </IconLine>
    )}
  />
)
