import { Trans } from '@lingui/macro'
import React from 'react'
import { Redirect, RouteComponentProps } from 'react-router-dom'

import { CanMutate } from '../../../atoms/CanMutate'
import { Card } from '../../../atoms/Card'
import { Headline } from '../../../atoms/Headline'
import { MarginBox } from '../../../atoms/MarginBox'
import { NoteText } from '../../../atoms/NoteText'
import { VerticalGap } from '../../../atoms/VerticalGap'
import { i18n } from '../../../i18n'
import { FullPageLayout } from '../../../molecules/FullPageLayout'
import { TopRightButtonContainer } from '../../../molecules/TopRightButtonContainer'
import { IsCategoryType } from '../atoms/IsCategoryType'
import { useMenuRouting } from '../hooks/useMenuRouting'
import { getRecipeTypeLabel } from '../labels'
import { TRecipeType } from '../menutypes'
import { SubmitButton } from '../molecules/SubmitButton'
import { RecipeCategoryView } from '../organisms/RecipeCategoryView'
import { RecipeFormBasic } from '../organisms/RecipeFormBasic'
import { RecipeFormLoader } from '../organisms/RecipeFormLoader'
import { RecipeFormPricing } from '../organisms/RecipeFormPricing'
import { RecipeIngredientsView } from '../organisms/RecipeIngredientsView'

interface IProps extends RouteComponentProps<{ recipeId: ID }> {}

export const MenuRecipePage: React.FC<IProps> = ({ match }) => {
  const { recipeId } = match.params
  const { isCreate, itemType: recipeType, getRecipeListRoute } = useMenuRouting(
    recipeId,
  )

  if (recipeType === null) {
    return <Redirect to={getRecipeListRoute()} />
  }

  return (
    <RecipeFormLoader
      isCreate={isCreate}
      recipeId={recipeId}
      recipeType={recipeType}
    >
      <MarginBox margin="0 1.25rem 4rem 0">
        <FullPageLayout
          pageTitle={getRecipeTypeLabel(recipeType)}
          hideLogoutWidget={true}
          useBackButton={true}
        >
          <Card>
            <RecipeFormBasic recipeType={recipeType} />
          </Card>
          <Card>
            <RecipeFormPricing recipeType={recipeType} recipeId={recipeId} />
          </Card>
          <Card>
            <Headline>
              <Trans>Recipe ingredients</Trans>
            </Headline>
            <VerticalGap />
            <RecipeIngredientsView />
          </Card>
          <IsCategoryType type={recipeType} isMain={true}>
            <Card>
              <Headline>
                <Trans>Sidedish categories</Trans>
              </Headline>
              <NoteText>
                <Trans>These sidedish are presented with recipe</Trans>
              </NoteText>
              <RecipeCategoryView
                categoryType={TRecipeType.SideDish}
                field="sideDishes"
                addButtonText={i18n.t`Attach sidedish category`}
                noRowsText={i18n.t`Recipe has no sidedish category attached`}
              />
            </Card>
            <Card>
              <Headline>
                <Trans>Category assignments</Trans>
              </Headline>
              <NoteText>
                <Trans>Recipe is in these categories</Trans>
              </NoteText>
              <RecipeCategoryView
                categoryType={TRecipeType.Main}
                field="categories"
                addButtonText={i18n.t`Add to category`}
                noRowsText={i18n.t`Recipe is not in any category`}
              />
            </Card>
          </IsCategoryType>
        </FullPageLayout>

        <TopRightButtonContainer>
          <CanMutate
            mutationName={recipeId === null ? 'createRecipe' : 'updateRecipe'}
          >
            <SubmitButton isCreate={recipeId === null} />
          </CanMutate>
        </TopRightButtonContainer>
      </MarginBox>
    </RecipeFormLoader>
  )
}
