import React from 'react'
import { Route } from 'react-router-dom'

interface IProps {
  render?(goBack: () => void): ReactNode
}

export const NavigateBack: React.FC<IProps> = ({ render }) => (
  <Route>
    {({ history }) => {
      if (render) {
        return render(() => history.goBack())
      }
      history.goBack()
      return null
    }}
  </Route>
)
