import { Trans } from '@lingui/macro'
import _ from 'lodash'
import React from 'react'
import { IFormApi, IFormState } from 'react-form'

import { SelectControlFlat } from '../../../atoms/SelectControlFlat'
import { i18n } from '../../../i18n'
import { CategoryRequirementControl } from '../molecules/CategoryRequirementControl'

interface IRequirement {
  label: string
  min?: number
  max?: number
}

export const getRulesMap = (): Record<string, IRequirement> => ({
  noLimit: {
    label: i18n.t`No limitation`,
    min: 0,
    max: 0,
  },
  optional: {
    label: i18n.t`One at most`,
    min: 0,
    max: 1,
  },
  required: {
    label: i18n.t`One at least`,
    min: 1,
    max: 0,
  },
  onlyOne: {
    label: i18n.t`Exactly one`,
    min: 1,
    max: 1,
  },
  custom: {
    label: i18n.t`Custom range`,
  },
})

type TRule = keyof ReturnType<typeof getRulesMap>

export const CategoryRequirementsForm: React.FC<{
  formApi: IFormApi & IFormState
}> = ({ formApi }) => {
  const ruleMap = React.useMemo(getRulesMap, [])
  const [currentRule, setRule] = React.useState<TRule>('noLimit')

  const minCount = formApi.getValue('minCount')
  const maxCount = formApi.getValue('maxCount')

  React.useEffect(() => {
    if (currentRule !== 'custom') {
      const found = _.findKey(
        ruleMap,
        ({ min, max }) => min === minCount && max === maxCount,
      )
      if (found) {
        setRule(found)
      }
    }
  }, [currentRule, minCount, maxCount, ruleMap])

  const setMinCount = (count: number) => formApi.setValue('minCount', count)
  const setMaxCount = (count: number) => formApi.setValue('maxCount', count)

  return (
    <>
      <SelectControlFlat<TRule>
        items={Object.keys(ruleMap) as ReadonlyArray<TRule>}
        formatItem={req => ruleMap[req].label}
        onSingleSelection={selectedRule => {
          if (selectedRule) {
            setRule(selectedRule)
            const rule = ruleMap[selectedRule]
            setMinCount(rule.min || 0)
            setMaxCount(rule.max || 0)
          }
        }}
        selectedItem={currentRule}
      />
      <CategoryRequirementControl
        id="minCount"
        count={minCount}
        label={<Trans>Minimum</Trans>}
        enabled={currentRule === 'custom'}
        onChange={setMinCount}
      />
      <CategoryRequirementControl
        id="maxCount"
        count={maxCount}
        label={<Trans>Maximum</Trans>}
        enabled={currentRule === 'custom'}
        onChange={setMaxCount}
      />
    </>
  )
}
