import gql from 'graphql-tag'

import { buildQuery } from '../../../graph/buildQuery'
import { FMenuAllergenFragmentDoc } from '../../../graph/generated'
import { GAllergens } from '../../../graph/types'

export const AllergensQuery = gql`
  query GAllergens {
    allergens {
      ...FMenuAllergen
    }
  }
  ${FMenuAllergenFragmentDoc}
`

export const QueryAllergens = buildQuery<
  GAllergens.Query,
  GAllergens.Variables
>(AllergensQuery)
