import React from 'react'
import { Field, FieldType, IFieldApi } from 'react-form'

import { DataTableEmptyText } from '../../../atoms/DataTable'
import {
  FMenuCategoryListItemFragment,
  WareCategoryTypeEnum,
} from '../../../graph/generated'
import { RM } from '../../../helpers/ramda'
import { CategoryListTable } from '../molecules/CategoryListTable'
import CategorySuggestionPanel from './CategorySuggestionPanel'

interface IProps {
  categoryType: WareCategoryTypeEnum
  addButtonText: string
  noRowsText: string
  field: FieldType
}

type TCategory = FMenuCategoryListItemFragment

export const RecipeCategoryView: React.FC<IProps> = ({
  categoryType,
  addButtonText,
  noRowsText,
  field,
}) => (
  <Field field={field}>
    {(fieldApi: IFieldApi<TCategory[]>) => (
      <>
        <CategoryListTable
          categories={fieldApi.value}
          showRemoveColumn={true}
          showEditColumn={false}
          showHeader={false}
          onRemove={categoryId =>
            fieldApi.setValue(
              RM.reject(RM.propEq('id', categoryId), fieldApi.value),
            )
          }
          noRowsDisplay={<DataTableEmptyText text={noRowsText} />}
        />
        <CategorySuggestionPanel
          addButtonText={addButtonText}
          categoryType={categoryType}
          currentCategories={fieldApi.value}
          onAdd={addedCategory =>
            fieldApi.setValue(fieldApi.value.concat(addedCategory))
          }
        />
      </>
    )}
  </Field>
)
