import React from 'react'

import { CanMutate } from '../../../atoms/CanMutate'
import { CardDock } from '../../../atoms/CardDock'
import { FormColumn, FormContainer } from '../../../atoms/FormContainer'
import { i18n } from '../../../i18n'
import { WithSettings } from '../../settings/atoms/WithSettings'
import { FormDeleteButton } from '../molecules/FormDeleteButton'
import { FormInputAllergens } from '../molecules/FormInputAllergens'
import { FormInputAllowNegative } from '../molecules/FormInputAllowNegative'
import { FormInputCode } from '../molecules/FormInputCode'
import { FormInputInternalNote } from '../molecules/FormInputInternalNote'
import { FormInputName } from '../molecules/FormInputName'
import { FormInputPlu } from '../molecules/FormInputPlu'
import { FormInputWareUnit } from '../molecules/FormInputWareUnit'

export const StockFormMain: React.FC = props => (
  <FormContainer>
    <FormColumn>
      <FormInputName placeholder={i18n.t`Card name`} />
      <FormInputCode placeholder={i18n.t`Card code`} />
      <FormInputWareUnit placeholder={i18n.t`Choose unit`} />
      <FormInputPlu placeholder={i18n.t`PLU`} />
      <FormInputInternalNote
        placeholder={i18n.t`Card note`}
        field="note"
        label={i18n.t`Card note`}
      />
      <FormInputAllergens />
      <FormInputAllowNegative />
    </FormColumn>
    <CardDock position="BottomRight">
      <CanMutate mutationName="destroyStockItem">
        <WithSettings
          render={({ menu }) => (
            <FormDeleteButton
              confirmText={i18n.t`Remove this card?`}
              onDelete={id => menu.stock.deleteItem(id)}
            />
          )}
        />
      </CanMutate>
    </CardDock>
  </FormContainer>
)
