import 'react-virtualized/styles.css'

import React from 'react'
import {
  AutoSizer,
  List,
  ListProps,
  ListRowRenderer,
  WindowScroller,
} from 'react-virtualized'

interface IColumn {
  size: string
}

interface IDataListProps<T> extends Partial<ListProps> {
  items: ReadonlyArray<T>
  columns?: IColumn[]
  renderItem(item: T, index: number): ReactNode
  isLoading?: boolean
  heightStretch?: boolean
}

export function DataList<T>(props: IDataListProps<T>) {
  const {
    items,
    renderItem,
    heightStretch = false,
    rowHeight = 60,
    ...listProps
  } = props

  const rowRenderer: ListRowRenderer = React.useCallback(
    ({ key, style, index }) => (
      <div key={key} style={style}>
        {renderItem(items[index], index)}
      </div>
    ),
    [renderItem, items],
  )

  const renderList = (width: number, height: number) => (
    <List
      width={width}
      height={height}
      headerHeight={50}
      rowHeight={rowHeight}
      rowCount={items ? items.length : 0}
      rowRenderer={rowRenderer}
      style={{ outline: 'none' }}
      {...listProps}
    />
  )

  return heightStretch ? (
    <AutoSizer>{({ width, height }) => renderList(width, height)}</AutoSizer>
  ) : (
    <WindowScroller scrollElement={window}>
      {({ height }) => (
        <AutoSizer disableHeight={true}>
          {({ width }) => renderList(width, height)}
        </AutoSizer>
      )}
    </WindowScroller>
  )
}
