import React from 'react'
import { NestedField } from 'react-form'
import styled from 'styled-components'

import { FlexRowSingle } from '../../../atoms/FlexRowSingle'
import { FormDisplayField } from '../../../atoms/FormDisplayField'
import { Headline } from '../../../atoms/Headline'
import { TPriceType } from '../menutypes'
import { FormInputPrice } from './FormInputPrice'
import { FormInputVatSelect } from './FormInputVatSelect'

interface IProps {
  priceType: TPriceType
  label: string
}

const FormPricingCurrencyCode = styled(FormDisplayField)`
  padding: 0.5rem;
`

export const FormPricingField: React.FC<IProps> = ({ priceType, label }) => (
  <>
    <Headline>{label}</Headline>
    <NestedField field={['prices', priceType]}>
      <FlexRowSingle justify="flex-start">
        <FormInputPrice field="value" />
        <FormPricingCurrencyCode field="currencyCode" />
        <FormInputVatSelect field="vatId" />
      </FlexRowSingle>
    </NestedField>
  </>
)
